.myExpenses{
    margin-top: 100px;
}


.expMonth{
    margin-bottom: 30px;
    position: relative;
}

p{
    font-size: 20px;
}

button:focus{
    border: none;
    outline: none;
}

.editBtnPerMonth{
    position: absolute;
    top: 11px;
    right: 18px;
    background: transparent;
    border: none;
    font-size: 18px;
    cursor: pointer;
    color: black;
}

.expense-row{
    width: 100%;
    /* background-color: pink; */
    padding: 20px 30px;
}

.month-disp{
    width:100%; 
    height:50px; 
    background-color:aquamarine; 
    display:flex; 
    align-items:center; 
    justify-content: center;
}


.headerRow{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding-bottom: 10px;
    border-bottom: 1px solid black;
}

.outData{
    width: 100%;
    margin-top: 20px;
    padding: 0 30px;
    /* background-color: #fff; */
}

.inData{
    margin-bottom: 20px;
    /* background-color: red; */
    display: flex;
    align-items: center;
    /* height: 22px; */
    /* justify-content: space-between; */
}

.inData p:nth-child(2){
    margin-left: auto ;
}

.twoBtns{
    display: none;
    position: relative;
    left: 30px;
    gap: 5px;

}

.btnEdit, .btnDel{
    width: 22px;
    height: 22px;
    border-radius: 50%;
    border: 1px solid darkgray;
    cursor: pointer;
    transition: .5s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    color: black;
}
.btnEdit{
    background-color: aquamarine;
}

.btnDel{
    background-color: rgb(244, 144, 144);;
}

.btnEdit:hover, .btnDel:hover{
    background-color: orange;
    color: white;
}


button svg{
    pointer-events: none;
}

.viewModes{
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    gap: 30px;
    padding: 20px;
    
}

.btnViews{
    width: 100px;
    border: 1px solid darkgray;
    border-radius: 10px;
    font-size: 18px;
    cursor: pointer;
    color: black;
}

.btnViews.active{
    font-weight: bold;
    background-color: orange;
}

.refreshBtn{
    position: fixed;
    top: 90px;
    right: 40px;
    width: 60px;
    height: 60px;
    border-radius: 50px;
    border: 2px solid black;
    background-color: aquamarine;
    font-size: 25px;
    cursor: pointer;
    transition: .5s ease;
    display: grid;
    place-items: center;
    z-index: 2;
    color: black;
}

.addForm{
    color: black;
    position: fixed;
    bottom: 30px;
    right: 40px;
    width: 60px;
    height: 60px;
    border-radius: 50px;
    border: 2px solid black;
    background-color: aquamarine;
    font-size: 25px;
    cursor: pointer;
    transition: .5s ease;
    display: grid;
    place-items: center;
    z-index: 2;
}


#selMonth{
    width: 170px;
    text-align: center;
    height: 30px;
    color: black;
}

.listings{
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 10px;
    padding: 15px;
}

#printDataNow{
    background-color: white;
    place-self: center;
    width: 70%;
    width: 785px;
    border: 2px solid black;
    margin-bottom: 60px;
}


.formArea, .formArea2{
    background-color: rgba(0,0,0,0.4);
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 3;
    display: none;
}

#myForm, #myForm2{
    background-color: #D9D9D9;
    width: 100%;
    max-width: 500px;
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 2;
    display: flex;
    transform: translate(-50%,-50%);
    border: 2px solid black;
    border-radius: 20px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px;
    padding-top: 55px;
    gap: 20px;
    scale: 1.2;
    transform-origin: top left;
}

#myForm2{
    background-color: rgb(157 223 235);
}

#myForm button, #myForm2 button{
    position: absolute;
    top: 15px;
    right: 15px;
    width: 20px;
    height: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: white;
    background-color: rgb(225, 103, 103);
    border: 1px solid black;
}

.form-item{
    display: flex;
    width: 100%;
    height: 25px;
    /* background-color: yellow; */
    gap: 10px;
}

.form-item label{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    min-width: 70px;
    /* background-color:orange; */
}

input[type='text']{
    width: 100%;
    padding-left: 5px;
}

input[type='date']{
    padding-left: 5px;
    width: 200px;
    color: black;
}


input[type='submit']{
    width: 140px;
    height: 30px;
    border-radius: 10px;
    border: none;
    background-color: white;
    font-weight: bold;
    cursor: pointer;    
    transition: .5s ease;
    color: black;
}

input[type='submit']:hover{
    background-color: orange;
}


#graph-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 30px;
    margin-top: 10px;
}

#selMonth{
    width: 170px;
    text-align: center;
    height: 30px;
}
#graphFilter{
    width: 170px;
    text-align: center;
    height: 30px;
}

#myChart{
    background-color: white;
    padding: 40px;
    min-height: 350px;
    border: 2px solid black;
}

.userDetail{
    text-align: center;
    margin-top: 30px;
}


@media (max-width: 550px){
    .refreshBtn{
        position: fixed;
        top: auto;
        bottom: 30px;
        right: 120px;  
    }

    .userDetail{
        font-size: 30px;
    }

    #printDataNow{
        margin: 0 20px 60px 20px;
    }

    #myChart{
        margin: 0 20px;
        margin-bottom: 60px;
    }

    .outData{
        padding: 0;
    }

    .inData{
        margin-bottom: 10px;
        display: flex;
        gap: 10px;
        align-items: flex-start;
    }
    .month-disp{
        font-size: 23px;
    }
    .headerRow h3{
        font-size: 20px;
    }
    .inData p{
        font-size: 18px;
    }
    #myForm, #myForm2{
        width: 300px;
    }
    input[type='date']:before {
    color: black;
    content: 'mm/dd/yyyy';
    }
    input[type='date'].clicked:before {
        content: '';
    }
    .twoBtns{
        left: 0;
    }
    
}
